import { DisplayMode, Guid, ServiceScope, Validate } from '@microsoft/sp-client-base';
import { IClientSideWebPartManifest } from '@microsoft/sp-module-interfaces';

import CanvasWebPartHost from '../../../canvas/canvas/CanvasWebPartHost';
import CanvasControlType from '../../../canvas/canvas/CanvasControlType';
import ClientSideWebPartManager from '../../../../../clientSideWebParts/core/ClientSideWebPartManager';
import { ISerializedCanvasControl } from '../canvasControl/ISerializedCanvasControl';
import { IToolboxItem } from '../../toolbox/toolboxItem/IToolboxItem';
import { IToolboxProps } from '../../toolbox/toolbox/IToolbox';
import { PropertyPaneState } from '../../../../../common/webParts/PropertyPaneDefinitions';
import SPRte, { ISPRteProps } from '../../../sp-rte/rte/SPRte';
import WebPartZone, { IWebPartZoneProps } from '../../../canvas/sampleCanvasControls/WebPartZone';
import { CanvasA11yManager } from '../../a11y/CanvasA11y';

export default class CanvasStore {
  private _a11yManager: CanvasA11yManager;
  private _canvasElement: HTMLElement;
  private _controls: Map<string, SPRte | WebPartZone>;
  private _controlsProps: ISerializedCanvasControl[];
  private _deletedRow: number;
  private _displayMode: DisplayMode;
  private _handleCanvasChanged: () => void;
  private _hoveredControlId: string;
  private _isDeleteDialogOpen: boolean;
  private _oldActiveElement: Element;
  private _pollId: number;
  private _render: () => void;
  private _scrollableParentElement: HTMLElement;
  private _scrollThreshold: number;
  private _selectedControlId: string;
  private _rowToBeDeleted: number;
  private _toolboxProps: IToolboxProps;
  private _webPartManager: ClientSideWebPartManager;

  constructor(
    serviceScope: ServiceScope,
    render: () => void,
    mode: DisplayMode,
    serializedCanvas?: string,
    handleCanvasChanged?: () => void,
    scrollThreshold?: number
  ) {
    Validate.isNotNullOrUndefined(serviceScope, 'serviceScope');
    Validate.isNotNullOrUndefined(render, 'render');
    Validate.isNotNullOrUndefined(mode, 'mode');

    this._addReference = this._addReference.bind(this);
    this._notifyChanged = this._notifyChanged.bind(this);
    this.addControl = this.addControl.bind(this);
    this.closeToolbox = this.closeToolbox.bind(this);
    this.deleteControl = this.deleteControl.bind(this);
    this.handleConfigureButtonClicked = this.handleConfigureButtonClicked.bind(this);
    this.handleDeleteButtonClicked = this.handleDeleteButtonClicked.bind(this);
    this.handleDeleteDialogConfirmation = this.handleDeleteDialogConfirmation.bind(this);
    this.handleDeleteDialogDismiss = this.handleDeleteDialogDismiss.bind(this);
    this.handleWindowBlur = this.handleWindowBlur.bind(this);
    this.reclaimFocus = this.reclaimFocus.bind(this);
    this.scrollIntoView = this.scrollIntoView.bind(this);
    this.openToolbox = this.openToolbox.bind(this);

    this._render = render;
    this._controls = new Map<string, SPRte | WebPartZone>();
    this._controlsProps = [];
    this._displayMode = mode;
    this._handleCanvasChanged = handleCanvasChanged;
    this._isDeleteDialogOpen = false;
    this._pollActiveElement = this._pollActiveElement.bind(this);
    this._scrollThreshold = scrollThreshold || 0;
    this._webPartManager = new ClientSideWebPartManager(
      new CanvasWebPartHost(
        serviceScope,
        this.scrollIntoView.bind(this, 'full'),
        handleCanvasChanged,
        this.reclaimFocus)
    );

    this._toolboxProps = {
      isOpen: false,
      onItemClick: this.addControl.bind(this, 0),
      row: undefined,
      toolboxItems: [],
      verticalPosition: undefined
    };

    if (this._displayMode === DisplayMode.Edit) {
      this.webPartManager.fetchWebParts();
    }
  }

  public get addReference(): (ref: SPRte | WebPartZone) => void {
    return this._addReference;
  }

  public get a11yManager(): CanvasA11yManager {
    return this._a11yManager;
  }

  public set a11yManager(elem: CanvasA11yManager) {
    this._a11yManager = elem;
  }

  public get canvasElement(): HTMLElement {
    return this._canvasElement;
  }

  public set canvasElement(elem: HTMLElement) {
    this._canvasElement = elem;
  }

  public get controlsProps(): ISerializedCanvasControl[] {
    return this._controlsProps;
  }

  public get deletedRow(): number {
    return this._deletedRow;
  }

  public set deletedRow(row: number) {
    this._deletedRow = row;
  }

  public get displayMode(): DisplayMode {
    return this._displayMode;
  }

  public set displayMode(newMode: DisplayMode) {
    if (this._displayMode !== newMode) {
      this._displayMode = newMode;
      for (const props of this.controlsProps) {
        this._controls.get(props.id).setDisplayMode(newMode);
      }

      if (this._displayMode === DisplayMode.Read) {
        this._selectedControlId = undefined;
        this.webPartManager.configureStart(undefined, PropertyPaneState.Close);
      }

      if (this._displayMode === DisplayMode.Edit) {
        this.webPartManager.fetchWebParts();
      }

      this._render();
    }
  }

  /**
   * The hoveredControlId keeps track of which control the pointer is currently over.
   * It is used as a workaround to track pointer events on web parts that have IFrames.
   */
  public set hoveredControlId(id: string) {
    this._hoveredControlId = id;
  }

  public get isDeleteDialogOpen(): boolean {
    return this._isDeleteDialogOpen;
  }

  public get notifyChanged(): () => void {
    return this._notifyChanged;
  }

  public get scrollableParent(): HTMLElement {
    return this._scrollableParentElement;
  }

  public set scrollableParent(elem: HTMLElement) {
    this._scrollableParentElement = elem;
  }

  public get scrollThreshold(): number {
    return this._scrollThreshold;
  }

  public get selectedControlId(): string {
    return this._selectedControlId;
  }

  public set selectedControlId(id: string) {
    if (this._selectedControlId !== id) {
      this._selectedControlId = id;

      /**
       * If open, change the Property Pane to reflect the new selection. If the new selection is not a web part,
       * change the Property Pane to the empty state.
       */
      const control: SPRte | WebPartZone = this._controls.get(id);
      if (control && control.props.controlType !== CanvasControlType.WebPartZone) {
        this._webPartManager.configureStart(undefined);
      } else {
        this._webPartManager.configureStart(id);
      }

      this._render();
    }
  }

  public get toolboxProps(): IToolboxProps {
    this._toolboxProps.toolboxItems = [];

    /* tslint:disable:no-any */
    this._getManifestMap().forEach((manifest: IClientSideWebPartManifest<any>, cid: string) => {
    /* tslint:enable:no-any */
      const itemProps: IWebPartZoneProps = {
        controlType: CanvasControlType.WebPartZone,
        displayMode: DisplayMode.Edit,
        id: undefined,
        webPartId: cid,
        webPartData: undefined,
        webPartManifest: manifest
      };

      const toolboxItem: IToolboxItem = {
        itemProps,
        description: manifest.preconfiguredEntries[0].description.default,
        displayName: manifest.preconfiguredEntries[0].title.default,
        msIconName: manifest.preconfiguredEntries[0].officeFabricIconFontName
      };

      this._toolboxProps.toolboxItems.push(toolboxItem);
    });

    return this._toolboxProps;
  }

  public get webPartManager(): ClientSideWebPartManager {
    return this._webPartManager;
  }

  public addControl(
    row: number,
    itemProps: ISerializedCanvasControl
  ): void {
    this._selectedControlId = itemProps.id = Guid.newGuid().toString();
    this._addControl(row, itemProps);

    this._render();
    this._notifyChanged();
  }

  public deleteControl(row: number): void {
    this._deletedRow = row;
    this._controlsProps.splice(row, 1);

    if (this._controlsProps.length === 0) {
      this._webPartManager.configureStart(undefined, PropertyPaneState.Close);
    }

    this._render();
    this._notifyChanged();
  }

  public getControl(id: string): SPRte | WebPartZone {
    return this._controls.get(id);
  }

  public openToolbox(row: number, verticalPosition: number): void {
    if (this._toolboxProps.isOpen) {
      this.closeToolbox();
    }

    this._toolboxProps.isOpen = true;
    this._toolboxProps.onItemClick = this.addControl.bind(this, row);
    this._toolboxProps.row = row;
    this._toolboxProps.verticalPosition = verticalPosition;
    document.addEventListener('click', this.closeToolbox);

    this._render();
  }

  public closeToolbox(): void {
    this._toolboxProps.isOpen = false;
    this._toolboxProps.row = undefined;
    document.removeEventListener('click', this.closeToolbox);

    this._render();
  }

  public handleConfigureButtonClicked(id: string): void {
    const isCurrentlySelected: boolean = this.selectedControlId === id;

    // Only toggle the Property Pane if the currently selected control's configure button is clicked.
    (this._controls.get(id) as WebPartZone).handleConfigureButtonClicked(isCurrentlySelected);

    // Set private member so we don't call configureStart twice.
    this._selectedControlId = id;
    this._render();
  }

  public handleDeleteButtonClicked(row: number): void {
    this._rowToBeDeleted = row;

    const control: ISerializedCanvasControl = this._controlsProps[this._rowToBeDeleted];
    this._selectedControlId = control.id;
    this._webPartManager.configureStart(
      control.controlType === CanvasControlType.WebPartZone ? this._selectedControlId : undefined
    );
    this._isDeleteDialogOpen = true;

    this._render();
  }

  public handleDeleteDialogConfirmation(): void {
    const controlProps: ISerializedCanvasControl = this._controlsProps[this._rowToBeDeleted];

    if (controlProps.controlType === CanvasControlType.WebPartZone) {
      (this._controls.get(controlProps.id) as WebPartZone).handleDeleteButtonClicked();
    }

    this._isDeleteDialogOpen = false;
    this.deleteControl(this._rowToBeDeleted);
  }

  public handleDeleteDialogDismiss(): void {
    this._rowToBeDeleted = undefined;
    this._isDeleteDialogOpen = false;

    this._render();
  }

  /**
   * IFrame's swallow pointer events, so this is a workaround to detect when focus has shifted to
   * an IFrame. Additionally, to compensate for IFrame to IFrame interactions we poll for the document.activeElement.
   * If document.activeElement is not the same as it was in the last poll then the focus has shifted from the IFrame.
   * If the new document.activeElement is an IFrame continue polling, otherwise we can stop polling because focus
   * is back inside of the current document.
   */
  public handleWindowBlur(e: MouseEvent): void {
    if (this._hoveredControlId && document.activeElement.tagName === 'IFRAME') {
      this.selectedControlId = this._hoveredControlId;
      this._oldActiveElement = document.activeElement;

      // Before setting a new interval clear the last one, this prevents more than one poll to be active.
      window.clearInterval(this._pollId);
      this._pollId = window.setInterval(this._pollActiveElement, 250);
    }
  }

  public deserialize(serializedCanvas: string): void {
    this._controlsProps = [];
    this._controls = new Map<string, SPRte | WebPartZone>();

    if (serializedCanvas) {
      const wrapper: HTMLElement = document.createElement('div');
      wrapper.innerHTML = serializedCanvas;

      const rows: NodeListOf<Element> = wrapper.querySelectorAll('[data-sp-canvascontrol]');
      const rowLength: number = rows.length;
      for (let i: number = 0; i < rowLength; ++i) {
        const row: Element = rows[i];
        const controlProps: ISerializedCanvasControl = JSON.parse(decodeURI(row.getAttribute('data-sp-controldata')));
        controlProps.displayMode = this._displayMode;
        this._addControl(i, controlProps);
      }
    }
  }

  public reclaimFocus(): void {
    this._controls.get(this._selectedControlId).focus();
  }

  /**
   * If needed, scrolls element into view after the element is added to the Canvas.
   * Note: Javascript scrollIntoView is not consistent cross-browser
   *
   * @param type - Specifies whether to scroll if the 'full' web part is visible or a 'partial' part of the web part
   *   is visible
   * @param element - A rendered element inside the Canvas to scroll to
   * @param duration - The length of time the animation should take. Frames are calculated by duration / delta time.
   * @param margin - Optional. Margin to be scrolled into view. If not provided, margin is not scrolled into view
   */
  public scrollIntoView(type: 'full' | 'partial', element: HTMLElement, duration: number, margin: number = 0): void {
    if (!this.scrollableParent || !element) {
      return;
    }

    const viewportHeight: number = Math.max(document.documentElement.clientHeight, window.innerHeight || 0);
    const frameBottom: number = Math.min(viewportHeight, this.scrollableParent.getBoundingClientRect().bottom);

    const elementClientRect: ClientRect = element.getBoundingClientRect();

    // 'partial' checks bottom to see if the element is partially hidden after adding, if it is scroll into view
    // 'full checks if the entire web part is hidden, if it is scroll into view
    if ((type === 'partial' ? elementClientRect.bottom : elementClientRect.top) > frameBottom) {
      this._scrollElementIntoView(frameBottom, element, duration, margin);
    }
  }

  public serialize(): string {
    const body: HTMLElement = document.createElement('div');
    const controlRow: HTMLElement = document.createElement('div');
    controlRow.setAttribute('data-sp-canvascontrol', '');
    const componentIdElement: HTMLElement = document.createElement('div');
    componentIdElement.setAttribute('data-sp-componentId', '');
    const htmlPropertiesElement: HTMLElement = document.createElement('div');
    htmlPropertiesElement.setAttribute('data-sp-htmlproperties', '');

    for (const control of this._controlsProps) {
      const row: HTMLElement = controlRow.cloneNode() as HTMLElement;
      const controlData: ISerializedCanvasControl = this._controls.get(control.id).serialize();

      if (control.controlType === CanvasControlType.RTE) {
        row.innerHTML = (controlData as ISPRteProps).innerHTML;
      } else if (control.controlType === CanvasControlType.WebPartZone) {
        const componentId: HTMLElement = componentIdElement.cloneNode() as HTMLElement;
        componentId.textContent = (controlData as IWebPartZoneProps).webPartId;

        const htmlProperties: HTMLElement = htmlPropertiesElement.cloneNode() as HTMLElement;
        htmlProperties.innerHTML = (controlData as IWebPartZoneProps).webPartData.htmlProperties;

        row.appendChild(componentId);
        row.appendChild(htmlProperties);
      }

      row.setAttribute('data-sp-controldata', encodeURI(JSON.stringify(controlData)));
      body.appendChild(row);
    }

    const wrapper: HTMLElement = document.createElement('div');
    wrapper.appendChild(body);

    return wrapper.innerHTML;
  }

  public tryGeneratePreviewImageUrl(): string {
    const webpartIds: string[] = [];

    for (const control of this._controlsProps) {
      if (control.controlType === CanvasControlType.WebPartZone) {
        webpartIds.push(control.id);
      }
    }

    return this.webPartManager.tryGeneratePreviewImageUrl(webpartIds);
  }

  private _addControl(row: number, itemProps: ISerializedCanvasControl): void {
    if (!itemProps.id) {
      itemProps.id = Guid.newGuid().toString();
    }

    if (
      itemProps.controlType === CanvasControlType.WebPartZone &&
      !(itemProps as IWebPartZoneProps).webPartManifest
    ) {
      const webPartProps: IWebPartZoneProps = itemProps as IWebPartZoneProps;
      webPartProps.webPartManifest = this._getManifestMap().get(webPartProps.webPartId);
    }

    this._controlsProps.splice(row, 0, itemProps);
  }

  private _addReference(ref: SPRte | WebPartZone): void {
    if (ref) {
      this._controls.set(ref.props.id, ref);
    }
  }

  /**
   * Calculates the easing number using the quadratic in-out formula
   */
  private _calculateEasing(time: number, start: number, diff: number, duration: number): number {
    time /= duration / 2;
    if (time < 1) {
      return diff / 2 * time * time + start;
    } else {
      --time;
      return -diff / 2 * (time * (time - 2) - 1) + start;
    }
  }

  /* tslint:disable:no-any */
  private _getManifestMap(): Map < string, IClientSideWebPartManifest < any >> {
    const tempMap: Map<string, IClientSideWebPartManifest <any>> = new Map<string, IClientSideWebPartManifest<any>>();
    const manifests: IClientSideWebPartManifest < any > [] = this._webPartManager.getWebParts();
  /* tslint:enable:no-any */

    for (const manifest of manifests) {
      tempMap.set(manifest.id, manifest);
    }

    return tempMap;
  }

  private _pollActiveElement(): void {
    const activeElement: Element = document.activeElement;
    if (activeElement.tagName !== 'IFRAME') {
      window.clearInterval(this._pollId);
    } else if (this._oldActiveElement !== activeElement && this._hoveredControlId) {
      this._oldActiveElement = activeElement;
      this.selectedControlId = this._hoveredControlId;
    }
  }

  private _scroll(start: number, end: number, duration: number, time: number = 0): void {
    const diff: number = end - start;
    const deltaTime: number = 20;

    if (time < duration) {
      requestAnimationFrame(() => {
        time += deltaTime;
        this.scrollableParent.scrollTop = this._calculateEasing(time, start, diff, duration);
        this._scroll(start, end, duration, time);
      });
    }
  }

  private _scrollElementIntoView(frameBottom: number, element: HTMLElement, duration: number, margin: number): void {
    const elementClientRect: ClientRect = element.getBoundingClientRect();
    const scrollPosition: number = elementClientRect.bottom - this.canvasElement.getBoundingClientRect().top;

    const frameTop: number = Math.max(0, this.scrollableParent.getBoundingClientRect().top);
    const offset: number = frameTop - frameBottom + this._scrollThreshold + margin;

    const newScrollTop: number = scrollPosition + offset + this._scrollThreshold;

    this._scroll(this.scrollableParent.scrollTop, newScrollTop, duration);
  }

  private _notifyChanged(): void {
    if (this._handleCanvasChanged) {
      this._handleCanvasChanged();
    }
  }
}